.ce-assign-worker{
    
  
    .work-time{
        display: flex;
        align-items: center;
        .work-time-start-date
        {
            
            align-items: center;
            margin-right: 10px;
        }
        .work-time-start-time
        {
            display: flex;
            flex-direction: row;
            .ce-form-control
            {
                width: 80px;
            }

            > {
                width: '100%'
            }
        }
    
       
       
    }
     .ce-btn-plus
    {
        margin-left: 10px;
    }
    .ce-assign-worker-action
    {
        display: flex;
        justify-content: flex-end;
        align-items: center;

    }
   
}
.worker-row-item
{
    border:1px solid #DBDEDE;
}
.worker-action
{
    padding-left: 20px;
    padding-right: 20px;
}
.ce-assign-worker-record
    {
       
        margin-top: 15px;
        align-items: center;
        align-content: center;
    }
